<template>
    <div>
        <h3>用户编辑页面</h3>
        <textarea v-model="content" cols="30" rows="10"></textarea>
        <button @click="saveContent">保持</button>
        <ul>
            <li v-for="(item,index) in list" :key="index">
                <h3>{{item.title}}</h3>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                content: '',
                list:[]
            }
        },
        methods: {
            saveContent(){
                this.list.push({
                    title:this.content
                });
                this.content = '';
            }
        },
        beforeRouteLeave (to, from, next) {
            //console.log(to);
            if(this.content){
                alert("输入框有值确定要离开吗");
                next(false);
            }else{
                 next();
            }
        }
    }
</script>

